<template>
    <div>
        <!-- 头部开始 -->
        <img :src="bejing" class="img_header"/>
        <img class="img-QR" :src="qrcodes"/>
        <font class="loginBtn">欢迎登录</font>
        <!-- 头部结束 -->
        <div  name="plyReal" @click="requestController">
            <!-- 保单验真按钮 -->
            <img :src="bg1" class="policyBg"/>
            <img :src="imgBtn" class="policyBtn"/>
            <font class="policyFont">保单验真</font>
            <!-- 保单验真按钮 -->
        </div>
        <!-- 在线出单按钮 -->
        <div>
            <img :src="bg2" class="onlineBg"/>
            <img :src="olIc" class="onlineBtn" name="onlinePly"/>
            <font class="onlineFont">在线出单</font>
        </div>
        <!-- 在线出单按钮 -->

        <!-- 底部开始 -->
        <img :src="logoBg" class="footBanner"/>
        <font class="copyright">{{Copyright}}</font>
        <!-- 底部结束 -->
    </div>
</template>
<script>
import imgUrl from './source/bg@3x.png';
import QRCode from './source/二维码@3x.png';
import bj1 from './source/bg1@3x.png';
import bj2 from './source/bg2@3x.png';
import iconBtn from './source/Icon.png';
import iconOnline from './source/IconOnline.png';
import logo from './source/logo@3x.png';
import $ from 'jquery';
import {Toast} from 'mint-ui';
import axios from 'axios';
// import 
export default{
    name: 'home',
    data() {
        return {
            bejing:imgUrl,
            qrcodes:QRCode,
            bg1:bj1,
            bg2:bj2,
            imgBtn:iconBtn,
            logoBg:logo,
            olIc:iconOnline,
            Copyright:"©2018中路财产保险股份有限公司 版权所有"
        }
    },
    methods: {
        pageJump() {
            this.$router.push({
                name:'PolicyReal',
                params:{}
            })
        },
        requestController(){
        axios({
            method: 'post',
            url: 'NonVehiclePolicyList/infoList',
            params: {
                'name':'张三'
            }
        }).then(response => {
            this.dataList = response.data;
        }).catch(error => {
            alert(error);
        })
    }
    }
};
</script>
<style>
@import 'https://unpkg.com/mint-ui/lib/style.css';
.policyBtn{
    position:absolute;
    left:64px;
    top:340px;
    width:62px;
    height: 62px;
}
.onlineBtn{
    position:absolute;
    left:249px;
    top:340px;
    width:62px;
    height: 62px;
}
.policyBg{
    position: absolute;
    left: 15px;
    top: 314px;
    width:160px;
    height: 160px;
}
.onlineBg{
    position: absolute;
    left: 200px;
    top: 314px;
    width:160px;
    height: 160px;
}
.img_header{
    width: 379px;
    height: 259px;
    position: absolute;
    left: -1px;
    top:0px;    
}
.img-QR{
    width: 62px;
    height: 62px;
    position: absolute;
    left:159.5px;
    top:91px;
}
.loginBtn{
    font-family: PingFangSC-Regular;
    width: 72px;
    height: 13px;
    font-size: 11px;
    color:#FFFFFF;
    position: absolute;
    left:166.84px;
    top:165px;
    opacity: 1;
    letter-spacing: 0.07rem;
    word-spacing: 13px;
}
.policyFont{
    font-family: PingFangSC-Regular;
    width: 98px;
    height: 33px;
    font-size: 24px;
    color:#FFFFFF;
    position: absolute;
    left:46px;
    top:414px;
    opacity: 1;
    letter-spacing: 0.41px;
    word-spacing: 33px;
}
.onlineFont{
    font-family: PingFangSC-Regular;
    width: 98px;
    height: 33px;
    font-size: 24px;
    color:#FFFFFF;
    position: absolute;
    left:231px;
    top:414px;
    opacity: 1;
    letter-spacing: 0.41px;
    word-spacing: 33px;
}
.footBanner{
    position:absolute;
    left:0px;
    top:563px;
    width: 375px;
    height: 30px;
}
.copyright{
    font-family: PingFangSC-Regular;
    color: #000000 60%;
    font-size: 12px;
    position: absolute;
    left: 76px;
    top: 600px;
    width: 233px;
    height: 17px;
}
</style>




